@import "./colors";
@import "./queries";

.gallery {
  grid-column: full-start/full-end;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-areas: repeat(7, 5vh);
  grid-gap: 1.5rem;
  padding: 1.5rem;
  @include respond(tab-land) {
    grid-gap: 0.5rem;
  }

  &__item {
    &--1 {
      grid-column: 1/3;
      grid-row: 1/3;
    }
    &--2 {
      grid-column: 3/6;
      grid-row: 1/4;
    }
    &--3 {
      grid-column: 6/7;
      grid-row: 1/3;
    }
    &--4 {
      grid-column: 7/9;
      grid-row: 1/3;
    }
    &--5 {
      grid-column: 1/3;
      grid-row: 3/6;
    }
    &--6 {
      grid-column: 3/5;
      grid-row: 4/6;
    }
    &--7 {
      grid-column: 5/6;
      grid-row: 4/5;
    }
    &--8 {
      grid-column: 6/8;
      grid-row: 3/5;
    }
    &--9 {
      grid-column: 8/9;
      grid-row: 3/6;
    }
    &--10 {
      grid-row: 6/8;
      grid-column: 1/2;
    }
    &--11 {
      grid-row: 6/8;
      grid-column: 2/4;
    }
    &--12 {
      grid-row: 6/8;
      grid-column: 4/5;
    }
    &--13 {
      grid-row: 5/8;
      grid-column: 5/8;
    }
    &--14 {
      grid-row: 6/8;
      grid-column: 8/9;
    }
  }
  &_img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
}
